html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

.search-bar__input {
  color: #3d4e61;
  border: 1px solid #e1e8ed;
  border-radius: 4px;
  width: 100%;
  margin-top: 20px;
  font-family: Avenir, serif;
  font-weight: 300;
  display: flex;
  box-shadow: 0 1px 8px #3d4e611a;
}

