  line-height: 38px;
}

[data-qa="big"], [data-qa="small"] {
  position: relative;
}

[data-qa="big"]:before {
  content: "";
  z-index: 1;
  background-image: url("Search.ccd3c07b.svg");
  background-position: center;
  background-repeat: no-repeat;
